<template>
  <div>
    <div class="srcoll">
      <div v-for="(item,i) in DevArr"
           :key="i"
           class="left">
        <div class="left_top">
          <div class="heng"></div>
          <div>{{ item.title }}</div>
        </div>
        <div class="left_bottom"
             v-for="(child, i) in item.content"
             :key="i"
             @click="chooseDev(child)">
          <div>{{ child.devName }}</div>
          <van-icon :name="child.myStatus == true?'checked':'circle'"
                    :color="child.myStatus == true?'#ff7c42':'#C5CAD5'"
                    size="20px" />
        </div>
      </div>
    </div>
    <van-button class="bottom_btn"
                @click="keep">保存</van-button>
  </div>
</template>
<script>
import { GetBusinessRoomdevBydomain, PostBusinessRoomdevBondByhotel, PostBusinessRoomdevBondByroomscale } from '@/api/api_business_roomdev';
import { GetSystemDictDataByType } from '@/api/api_query';
import { Toast } from 'vant';
export default {
    data() {
        return {
            DevArr: [],
            choosedArr: [],
            ids: []
        };
    },
    created() {
        this.$store.commit('setHeader', this.$route.query.active == '1' ? '请选择酒店设施' : '请选择房型设施');
        if (this.$route.query.items && this.$route.query.items.length > 0) {
            this.$route.query.items.map((item) => {
                this.ids.push(item.id);
                item.myStatus = true;
                this.choosedArr.push(item);
            });
        }
        this.getType();
    },
    methods: {
        getType() {
            GetSystemDictDataByType({ dictType: 'business_room_dev_type' }).done((res) => {
                this.dictDate = res.data;
                this.getDevInfo();
            });
        },
        // 根据设备范畴查询设施设备、1：酒店设施 2、房型设施
        getDevInfo() {
            GetBusinessRoomdevBydomain({ devDomain: this.$route.query.active }).done((res) => {
                if (this.ids && this.ids.length > 0) {
                    let idsObj = { ...this.ids };
                    for (const k in idsObj) {
                        res.data.map((item, i) => {
                            if (idsObj[k] == item.id) {
                                res.data.splice(i, 1);
                            }
                        });
                    }
                    res.data = res.data.concat(this.choosedArr);
                    res.data.sort((a, b) => {
                        return a.id - b.id;
                    });
                } else {
                    res.data.map((item) => {
                        item.myStatus = false;
                    });
                }
                let obj = {};
                res.data.forEach((item) => {
                    if (!item.myStatus) {
                        item.myStatus = false;
                    }
                    if (obj[item.devType]) {
                        obj[item.devType].push(item);
                    } else {
                        obj[item.devType] = [item];
                    }
                });
                let arr = [];
                for (const k in obj) {
                    let newObj = {};
                    newObj.devType = k;
                    newObj.title = this.dictDate.find((item) => item.dictValue == k).dictLabel;
                    newObj.content = obj[k];
                    arr.push(newObj);
                }
                this.DevArr = arr;
            });
        },
        chooseDev(item) {
            item.myStatus = !item.myStatus;
            if (item.myStatus == true) {
                this.choosedArr.push(item);
            } else {
                this.choosedArr = this.choosedArr.filter((itm) => itm.id != item.id);
            }
        },
        // 保存
        keep() {
            this.choosedArrFilter = this.choosedArr.filter((item) => item.myStatus == true);
            if (this.choosedArrFilter.length == 0) {
                Toast({
                    message: this.$route.query.active == '1' ? '请选择酒店设施' : '请选择房型设施',
                    duration: 900
                });
                return;
            }
            let roomDevIds = this.choosedArrFilter.map((item) => item.id);
            if (this.$route.query.active == '1') {
                let data = {
                    hotelId: this.$route.query.hotelId,
                    roomDevIds: roomDevIds.toString()
                };
                PostBusinessRoomdevBondByhotel(data).done((res) => {
                    Toast({
                        message: res.msg,
                        duration: 900
                    });
                    setTimeout(() => {
                        this.$router.go(-1);
                    }, 1000);
                });
            } else {
                let data = {
                    roomScaleId: this.$route.query.roomScaleId,
                    roomDevIds: roomDevIds.toString()
                };
                PostBusinessRoomdevBondByroomscale(data).done((res) => {
                    Toast({
                        message: res.msg,
                        duration: 900
                    });
                    setTimeout(() => {
                        this.$router.go(-1);
                    }, 1000);
                });
            }
        }
    }
};
</script>
<style scoped>
.left {
    text-align: left;
    background: #fff;
    margin-top: 20px;
    color: #000;
    font-size: 14px;
}
.left_top {
    display: flex;
    padding: 15px;
    border-bottom: 1px solid #f1eff2;
}
.heng {
    width: 4px;
    height: 14px;
    background: #2f7afa;
    border-radius: 26px;
    margin: auto 0;
    margin-right: 6px;
}
.left_bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #eee;
    margin: 0 20px;
    padding: 16px 0;
}
.srcoll {
    position: fixed;
    top: 40px;
    left: 0;
    right: 0;
    overflow: auto;
    height: 86%;
}
.bottom_btn {
    background: #ff7c42;
    color: #fff;
    width: 100%;
    border-radius: 4px;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
}
</style>



