import * as API from './';

/********************************* 设施设备 ********************************/ 
// 根据酒店查询设施设备
export const GetBusinessRoomdevByhotel = (data) => API.GET(`/api/business/roomdev/byhotel/${data.hotelId}`, data, true);
// 根据设备范畴查询设施设备
export const GetBusinessRoomdevBydomain = (data) => API.GET(`/api/business/roomdev/bydomain/${data.devDomain}`, data, true);
// 绑定酒店设施设备
export const PostBusinessRoomdevBondByhotel = (data) => API.POST(`/api/business/roomdev/bond/byhotel`, data, true);
// 绑定基础房型和设施设备
export const PostBusinessRoomdevBondByroomscale = (data) => API.POST(`/api/business/roomdev/bond/byroomscale`, data, true);



